.catalog {

    &__centered{
        @media screen and (max-width: 600px) {
        align-items: center;
        display: flex;
        justify-content: center;
        margin-bottom: 25px;
        }
    }

    a{
        text-decoration: none;
        color: #333;
    }
   
    display: flex;
    flex-wrap: wrap;
    margin: 115px 0px;
    text-align: center;
    @media screen and (max-width: 600px) {
        margin: 60px 0px; 
        width: 100%;
            
    }

   h1{
    font-size: 28px;
    margin: 40px 0;
    text-transform: uppercase;
   }

    img {
        width: 100%;
    }
    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        border: 1px solid #dbdbdb;
        padding: 8px;
        text-align: center;
        height: 100%;

        &:hover{
            border: 1px solid $primary;
        }

        @media screen and (max-width: 600px) {
            width: 80%;
            align-items: center;
        }

       
    }

    &__price {
        font-weight: bold;
        margin-top: 20px;
        color: $primary;
        padding: 20px;
        @media screen and (max-width: 600px) {
            padding: 0;
        }
    }

    &__img{
        border: 1px solid #dbdbdb;
    }

    &__title{
       
        margin-top: 20px;
        
        height: 100%;
    }
}
